import { MatrixActivateSimExe } from "src/app/models/cardMatrix/MatrixActivateSimExe";
import { MatrixRequisitionSimExe } from "src/app/models/cardMatrix/MatrixRequisitionSimExe";
import { AgentifaiRequestFactoryService } from "src/app/services/agentifai/agentifai-request-factory.service";

export class CredentialsManagementUtils {

    static createMatrixRequisitionSimExe(parameters: any, requestFactory: AgentifaiRequestFactoryService) : MatrixRequisitionSimExe {
        const request = new MatrixRequisitionSimExe()
        request.operationId = parseInt(parameters.operationId)
        request.forceDuplicateOperation = parameters.forceDuplicateOperation
        request.operationName = ''
        request.scheduling = requestFactory.createScheduling(parameters)
        request.adressSequenceNumber = parseInt(parameters.addressSequenceNumber)
        request.motive = requestFactory.createGenericKeyValue(parameters["motive"])

        return request
    }

    static createMatrixActivateSimExe(parameters: any, requestFactory: AgentifaiRequestFactoryService): MatrixActivateSimExe {
        const request = new MatrixActivateSimExe()
        request.operationId = parseInt(parameters.operationId)
        request.forceDuplicateOperation = parameters.forceDuplicateOperation
        request.operationName = ''
        request.scheduling = requestFactory.createScheduling(parameters)
        request.matrixReferenceNumber = parameters.matrixReferenceNumber
        request.hasNifCreds = parameters.hasNifCreds

        return request
    }

}