import { GenericOperationRequest } from '../GenericOperationRequest';
import { GenericPersonalCreditWorkElement } from './GenericPersonalCreditWorkElement';

export class ConsumerLoanAdhesionRequest extends GenericOperationRequest {
    targetAmount: number;
    periodMonths: number;
    includeDocuments: boolean;
    periodYears: number;
    fullKeyToAccount: string;
    currency: string;
    receiptsVenc : any;
    irs : any;
    laboralVinc : any;
    questions: any[];
    professionProof: any;
    addressProof: any;

    // campos profissionais
    profissao: GenericPersonalCreditWorkElement;
    sector: GenericPersonalCreditWorkElement;
    contrato: GenericPersonalCreditWorkElement;
    trabalho: GenericPersonalCreditWorkElement;
    antiguidade: number;
    codigoIRS: string;

    // campos financeiros
    nrAgregado: number;
    rendimentoBruto: number;  // exemplo: 11111
    rendimentoLiquido: number;  // exemplo: 11111
    despesasFamiliares: number;  // exemplo: 11111
    encargosMensais: number;  // exemplo: 11111

    // outros
    prestacao: string; // exemplo: 75,89
    addressDoc: any;
    professionDoc: any;
    citzeanCardDoc: any;
    ral: boolean;
    
    // Otimização de produto
    defaultSimulation: boolean; // para distinguir simulações base de simulações com dados do cliente
    financedLifeInsurance: boolean; // para indicar se o cliente quer seguro de vida
	useProd57: boolean; // para distinguir que produto está a ser usado
}