import { Scheduling } from 'src/app/models/operation/Scheduling';

export class GenericOperationRequest {
    operationId: number;
    operationName: string;
    operationEmail: string;
    forceDuplicateOperation: boolean;
    scheduling: Scheduling

    constructor() {
        this.operationId = null;
        this.operationName = '';
        this.forceDuplicateOperation = false;
        this.scheduling = new Scheduling();
    }
}