import { GenericOperationRequest } from 'src/app/models/operation/request/GenericOperationRequest'
import { Charges } from 'src/app/models/operation/Charges';

export class TransferInternalRequest extends GenericOperationRequest {


    charges: Charges[];
    sourceAccount: string;
    destinationAccount: string;
    destinationAccountCurrency: string;
    amount: string;
    currency: string;
    descriptionForSourceAccount: string;
    descriptionForDestinationAccount: string;   
    finality: string;
    comment: string;

    constructor() {
        super();
        this.charges = [new Charges()];
        this.sourceAccount = '';
        this.destinationAccount = '';
        this.destinationAccountCurrency = '';
        this.amount = '';
        this.currency = '';
        this.descriptionForSourceAccount = '';
        this.descriptionForDestinationAccount = '';     
        this.finality = '';
        this.comment = '';
    }

    get getSourceAccount(): any {
        return this.sourceAccount;
    }

    set setSourceAccount(sourceAccount: any) {
        this.sourceAccount = sourceAccount;
    }

    get getDestinationAccountAccount(): any {
        return this.destinationAccount;
    }

    set setDestinationAccountAccount(destinationAccount: any) {
        this.destinationAccount = destinationAccount;
    }

    get getDestinationAccountCurrency(): any {
        return this.destinationAccountCurrency;
    }

    set setDestinationAccountCurrency(destinationAccountCurrency: any) {
        this.destinationAccountCurrency = destinationAccountCurrency;
    }

    get getAmount(): string {
        return this.amount;
    }

    set setAmount(amount: string) {
        this.amount = amount;
    }

    get getCurrency(): any {
        return this.currency;
    }

    set setCurrency(currency: any) {
        this.currency = currency;
    }

    get getDescriptionForSourceAccount(): any {
        return this.descriptionForSourceAccount;
    }

    set setDescriptionForSourceAccount(descriptionForSourceAccount: any) {
        this.descriptionForSourceAccount = descriptionForSourceAccount;
    }

    get getdescriptionForDestinationAccount(): any {
        return this.descriptionForDestinationAccount;
    }

    set setdescriptionForDestinationAccount(descriptionForDestinationAccount: any) {
        this.descriptionForDestinationAccount = descriptionForDestinationAccount;
    }

    get getFinality(): string {
        return this.finality;
    }

    set setFinality(finality: string) {
        this.finality = finality;
    }

    get getComment(): string {
        return this.comment;
    }

    set setComment(comment: string) {
        this.comment = comment;
    }
}