import { Injectable } from '@angular/core';
import { EnvironmentService } from 'src/app/services/environment-service/environment-service.service';
import { StorageService } from 'src/app/services/storage/storage.service';

@Injectable({
  providedIn: 'root'
})
export class AssistantUtilsService {

  constructor(
    private environment: EnvironmentService,
    private storage: StorageService
  ) { }

  resetDefaultConfig() {
    const config = this.getDefaultAssistantConfig()
    return this.setConfig(config.apiUrl, config.apiKey, config.settings)
  }

  getConfig() {
    const apiUrl = this.getApiUrl()
    const apiKey = this.getApiKey()
    const settings = this.getSettings()

    return Promise.all([apiUrl, apiKey, settings])
  }

  setConfig(apiUrl: string, apiKey: string, settings: any) {
    return Promise.all([
      this.storage.setApiUrl(apiUrl), 
      this.storage.setApiKey(apiKey),
      this.storage.setSettings(settings)
    ])
  }

  private getApiUrl(): Promise<string> {
    const defaultApiUrl = this.getDefaultAssistantConfig().apiUrl

    return new Promise((resolve, _) => {
      this.storage.getApiUrl()
        .then(result => {
          if (result != null) {
            resolve(result)
          } else {
            resolve(defaultApiUrl)
          }
        })
        .catch(() => {
          resolve(defaultApiUrl)
        })
    })
  }

  private getApiKey(): Promise<string> {
    const defaultApiKey = this.getDefaultAssistantConfig().apiKey

    return new Promise((resolve, _) => {
      this.storage.getApiKey()
        .then(result => {
          if (result != null) {
            resolve(result)
          } else {
            resolve(defaultApiKey)
          }
        })
        .catch(() => {
          resolve(defaultApiKey)
        })
    })
  }

  private getSettings(): Promise<any> {
      const defaultSettings = this.getDefaultSettings()

      return new Promise((resolve, _) => {
        this.storage.getSettings()
        .then(result => {
          if(result != null) {
            resolve(result)
          } else {
            resolve(defaultSettings)
          }
        })
        .catch(() => {
          resolve(defaultSettings)
        })
      })
  }

  private getDefaultAssistantConfig() {
    return this.environment.getAgentifaiConfiguration();
  }

  private getDefaultSettings(): any {
    return {
      defaultTheme: "light",
      themes: {
        light: "AssistantConfiguration.json",
        dark: "AssistantConfigurationDark.json"
      }
    }
  }

}
