import { Injectable } from '@angular/core';
import { GenericKeyValue } from 'src/app/models/GenericKeyValue';
import { Credential } from 'src/app/models/operation/Credential';
import { Scheduling } from 'src/app/models/operation/Scheduling';

@Injectable({
  providedIn: 'root'
})
export class AgentifaiRequestFactoryService {

  createCredentials(parameters: any): Credential {
    const credentialJson = parameters.credential;
    if (credentialJson) {
      const credential = new Credential();
      credential.type = credentialJson.type;
      credential.value = credentialJson.value;
      return credential
    }
  }

  createScheduling(parameters: any): Scheduling {
    const schedulingParams = parameters.scheduling;
    const scheduling = new Scheduling();
    scheduling.startDate = schedulingParams.startDate;
    scheduling.endDate = schedulingParams.endDate || "";
    return scheduling;
  }

  parseBool(val: any) {
    return !!JSON.parse(String(val).toLowerCase());
  }

  createGenericKeyValue(parameters: any): GenericKeyValue {
    return new GenericKeyValue(parameters.key, parameters.value);
  }

  createGenericKeyValueList(parameters: any[]): GenericKeyValue[] {
    return parameters.map(item => {
      return new GenericKeyValue(item.key, item.value);
    })
  }
  
}
