import { Injectable } from '@angular/core';
import { ResponseEvent } from 'src/app/agentifai/models/agentifai-response-event';
import { AgentifaiUtils } from 'src/app/agentifai/utils/agentifai-utils';
import { WidgetResponse } from 'src/app/agentifai/models/agentifai-widget-response';

@Injectable({
  providedIn: 'root'
})
export class AgentifaiResponseFactoryService {

  createResponseFromRestApi(response: any, success: boolean, nonAnonymizableRegexesPaths: String[]): WidgetResponse {
    const event = success ? this.createResponseEventFromRestApi(response) : this.createErrorResponseEventFromRestApi(response);
    return new WidgetResponse(event, nonAnonymizableRegexesPaths);
  }

  //Create ResponseEvent Factory
  private createResponseEventFromRestApi(response: any): ResponseEvent {
    return this.createResponseEvent(response.headers, response.status, AgentifaiUtils.mapFromString(response.data));
  }

  private createErrorResponseEventFromRestApi(response: any): ResponseEvent {
    return this.createResponseEvent(response.headers, response.status, AgentifaiUtils.mapFromString(response.error));
  }

  private createResponseEvent(headers: JSON, status: number, body: JSON): ResponseEvent {
    return new ResponseEvent(headers, status, body)
  }

}

