import { Injectable } from '@angular/core';
import { AgentifaiResponse } from 'src/app/agentifai/models/agentifai-response';
import { AgentifaiRequest } from 'src/app/agentifai/models/agentifai-request';
import { AgentifaiCommand } from 'src/app/agentifai/commands/commom/agentifai-command';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { AgentifaiCommandType } from 'src/app/agentifai/commands/commom/agentifai-command-type';
import { AgentifaiUtils } from 'src/app/agentifai/utils/agentifai-utils';
import { AgentifaiCommandFactoryService } from 'src/app/services/agentifai/agentifai-command-factory.service';
import { AgentifaiServicesProvider } from 'src/app/services/agentifai/agentifai-services.service';

@Injectable({
  providedIn: 'root'
})
export class AgentifaiManagerProvider {

  private static commandKey: string = "requestType";

  constructor(
    private service: AgentifaiServicesProvider,
    private commandsFactory: AgentifaiCommandFactoryService
  ) { }

  executeWidgetRequest(data: JSON): Observable<AgentifaiResponse> {
    const request = this.createRequest(data);
    const commandValue: string = request.data[AgentifaiManagerProvider.commandKey];
    const commandType: AgentifaiCommandType = commandValue as AgentifaiCommandType;
    const command: AgentifaiCommand<any> = this.commandsFactory.createCommand(commandType, request);

    try {
      return this.executeCommand(command, request.id);
    } catch (error) {
      return this.handleError(request.id, error.toString())
    }
  }

  private executeCommand(command: AgentifaiCommand<any>, requestId: string): Observable<AgentifaiResponse> {
    return command.execute(this.service)
      .pipe(
        map(result => this.createSuccessResponse(requestId, result)),
        catchError(error => this.handleError(requestId, error))
      );
  }

  private createSuccessResponse(requestId: string, response: Object): AgentifaiResponse {
    const json = AgentifaiUtils.mapFromAny(response);
    return new AgentifaiResponse(requestId, true, json);
  }

  private createFailedResponse(requestId: string, errorResponse: any | Error) {
    const json = AgentifaiUtils.mapFromAny(errorResponse);
    return new AgentifaiResponse(requestId, false, json);
  }

  private handleError(requestId: string, error: any | Error) {
    const errorResponse = this.createFailedResponse(requestId, error);
    return throwError(errorResponse);
  }

  private createRequest(data: JSON): AgentifaiRequest {
    const id = data["requestId"];
    delete data["requestId"];
    return new AgentifaiRequest(id, data);
  }

}
