import { GenericOperationRequest } from 'src/app/models/operation/request/GenericOperationRequest'
import { Charges } from 'src/app/models/operation/Charges';

export class TransferSEPARequest extends GenericOperationRequest {


    charges: Charges[];
    sourceAccount: any;
    destinationAccount: any;
    amount: any;
    currency: string;
    description: string;
    additionalInfo: string;
    destinationName: string;
    finality: string;
    comment: string;
    urgent: boolean;
    commissionFee: number;
    stampDuty: number;
    warningCharge: number;
    warningVAT: number;
    chargesSum: number;
    chargesSumCurrency: string;

    constructor() {
        super();
       
        this.charges = [new Charges()];
        this.sourceAccount = '';
        this.destinationAccount = '';
        this.amount = '';
        this.currency = '';
        this.description = '';
        this.additionalInfo = '';
        this.destinationName = '';
        this.finality = '';
        this.comment = '';
        this.urgent = false;
        this.commissionFee = null;
        this.stampDuty = null;
        this.warningCharge = null;
        this.warningVAT = null;
        this.chargesSum = null;
        this.chargesSumCurrency = '';

    }

    get getSourceAccount(): any {
        return this.sourceAccount;
    }

    set setSourceAccount(sourceAccount: any) {
        this.sourceAccount = sourceAccount;
    }

    get getDestinationAccount(): any {
        return this.destinationAccount;
    }

    set setDestinationAccount(destinationAccount: any) {
        this.destinationAccount = destinationAccount;
    }

    get getAmount(): any {
        return this.amount;
    }

    set setAmount(amount: any) {
        this.amount = amount;
    }

    get getCurrency(): any {
        return this.currency;
    }

    set setCurrency(currency: any) {
        this.currency = currency;
    }

    get getDescription(): string {
        return this.description;
    }

    set setDescription(description: string) {
        this.description = description;
    }

    get getAdditionalInfo(): string {
        return this.additionalInfo;
    }

    set setAdditionalInfo(additionalInfo: string) {
        this.additionalInfo = additionalInfo;
    }

    get getDestinationName(): string {
        return this.destinationName;
    }

    set setDestinationName(destinationName: string) {
        this.destinationName = destinationName;
    }

    get getFinality(): string {
        return this.finality;
    }

    set setFinality(finality: string) {
        this.finality = finality;
    }

    get getComment(): string {
        return this.comment;
    }

    set setComment(comment: string) {
        this.comment = comment;
    }

    get getUrgent(): boolean {
        return this.urgent;
    }

    set setUrgent(urgent: boolean) {
        this.urgent = urgent;
    }

    get getCommissionFee(): number {
        return this.commissionFee;
    }

    set setCommissionFee(commissionFee: number) {
        this.commissionFee = commissionFee;
    }

    get getStampDuty(): number {
        return this.stampDuty;
    }

    set setStampDuty(stampDuty: number) {
        this.stampDuty = stampDuty;
    }

    get getWarningCharge(): number {
        return this.warningCharge;
    }

    set setWarningCharge(warningCharge: number) {
        this.warningCharge = warningCharge;
    }

    get getWarningVAT(): number {
        return this.warningVAT;
    }

    set setWarningVAT(warningVAT: number) {
        this.warningVAT = warningVAT;
    }

    get getChargesSum(): number {
        return this.chargesSum;
    }

    set setChargesSum(chargesSum: number) {
        this.chargesSum = chargesSum;
    }

    get getChargesSumCurrency(): string {
        return this.chargesSumCurrency;
    }

    set setChargesSumCurrency(chargesSumCurrency: string) {
        this.chargesSumCurrency = chargesSumCurrency;
    }

}