import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AgentifaiAccountsTransformerService {

  transformAccount(account: any): any {
    return this.transform(account);
  }

  transformAccounts(accounts: [any]): [any] {
    var accounts = accounts;

    accounts.forEach(account => {
      account = this.transform(account);
    });

    return accounts;
  }

  private transform(account: any): any {
    return this.addNewAccountProperties(
      account
    );
  }

  private addNewAccountProperties(account: any): any {
    account.accountName = account.description != null ? account.description.split(" - ").reverse()[0] : null;
    return account;
  }

}