import { Injectable } from '@angular/core';
import { AgentifaiManagerProvider } from 'src/app/services/agentifai/agentifai-manager.service';
import { AgentifaiResponse } from 'src/app/agentifai/models/agentifai-response';
import { EnvironmentService } from 'src/app/services/environment-service/environment-service.service';
import { OpenAliceIntent } from 'src/app/agentifai/open-alice-intent';
import { AgentifaiWidgetDimensions } from 'src/app/agentifai/models/widget-config/agentifai-widget-dimensions';

declare let agentifai: any;

@Injectable({
  providedIn: 'root'
})
export class AgentifaiPluginControllerService {

  constructor(
    private agentifaiManager: AgentifaiManagerProvider,
    private env: EnvironmentService
  ) { }

  initialize() {
    if (agentifai) {
      const config = this.env.getAgentifaiConfiguration();
      this.initializeWithConfig(config.apiUrl, config.apiKey, config.settings);
    }
  }

  initializeWithConfig(apiUrl: string, apiKey: string, settings: any) {
    if (agentifai) {
      this.addCallback();
      agentifai.initialize(apiUrl, apiKey, settings);
    }
  }

  stop() {
    if (agentifai) {
      agentifai.stop()
    }
  }

  setConfigurations(configurations: any) {
    if (agentifai) {
      agentifai.setConfigurations(configurations)
    }
  }

  showWidget() {
    if (agentifai) {
      agentifai.setWidgetVisibility(true);
    }
  }

  hideWidget() {
    if (agentifai) {
      agentifai.setWidgetVisibility(false);
    }
  }

  showApp(intent: OpenAliceIntent = null) {
    if (agentifai) {
      agentifai.openApplication(intent);
    }
  }

  hideApp() {
    if (agentifai) {
      agentifai.closeApplication();
    }
  }

  registerUser(id: string, name: string, token: string, authHeaders: any) {
    if (agentifai) {
      const user = { "id": id, "name": name, "token": token };
      agentifai.registerUser(user, authHeaders);
    }
  }

  unregisterUser() {
    if (agentifai) {
      agentifai.unregisterUser();
    }
  }

  addCallback() {
    if (agentifai) {
      agentifai.addClientCallback(data => {
        this.processAgentifaiEvent(data);
      }, () => {} );
    }
  }

  setWidgetDimensions(dimensions: AgentifaiWidgetDimensions) {
    if (agentifai) {
      agentifai.setWidgetDimensions(dimensions);
    }
  }

  setTheme(theme: string) {
    if (agentifai) {
      agentifai.setTheme(theme)
    }
  }

  private processAgentifaiEvent(data: JSON) {
    const eventType = data["type"];

    if (eventType == "applicationStatus") {
      this.processApplicationStatusEvent(data);
    } else if (eventType == "request") {
      this.executeAgentifaiRequest(data);
    }
  }

  private processApplicationStatusEvent(data: JSON) {
    const isOpened = data["isOpened"];
    if (isOpened) {
      this.env.openAlice();
    } else {
      this.env.closeAlice();
    }
  }

  private executeAgentifaiRequest(data: JSON) {
    console.log("REQUEST:" + JSON.stringify(data));
    this.agentifaiManager.executeWidgetRequest(data)
      .subscribe(resp => {
        this.sendResponseOfRequest(resp);
      }, error => {
        this.sendResponseOfRequest(error);
      });
  }

  private sendResponseOfRequest(response: AgentifaiResponse) {
    console.log("RESPONSE: " + JSON.stringify(response));
    agentifai.requestResult(response.requestId, response.success, response.result);
  }

}
