import { AgentifaiCommand } from 'src/app/agentifai/commands/commom/agentifai-command';
import { CredentialUnlockAnswersRequest } from 'src/app/models/operation/request/customers/CredentialUnlockAnswersRequest';
import { WidgetResponse } from 'src/app/agentifai/models/agentifai-widget-response';
import { AgentifaiRequest } from 'src/app/agentifai/models/agentifai-request';
import { AgentifaiResponseFactoryService } from 'src/app/services/agentifai/agentifai-response-factory.service';
import { map, catchError } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';
import { AgentifaiServicesProvider } from 'src/app/services/agentifai/agentifai-services.service';
import { Anonymization } from 'src/app/agentifai/utils/anonymization-utils';

export class UnlockCredentials implements AgentifaiCommand<WidgetResponse> {

    private nonAnonymizableRegexesPathsSuccess: String[] = [
        "headers.+",
        "status",
        "body\.(operationId|success)"
    ]

    private nonAnonymizableRegexesPathsError: String[] = Anonymization.NonAnonymizablePathRegexDefault;

    constructor(
        private request: AgentifaiRequest,
        private responseFactory: AgentifaiResponseFactoryService
    ) { }

    execute(service: AgentifaiServicesProvider): Observable<WidgetResponse> {
        const headers = this.request.getHeaders()
        const parameters: any = this.request.getParameters()
        const request = this.createRequest(parameters);

        return service.credentialUnlockAnswers(headers, request)
            .pipe(
                map(resp => this.createSuccessResponse(resp)),
                catchError(resp => this.createErrorResponse(resp))
            );
    }

    private createRequest(parameters: any): CredentialUnlockAnswersRequest {
        const request = new CredentialUnlockAnswersRequest();
        request.answers = parameters.answers;
        request.contractNumber = parameters.contractNumber;
        request.operationId = parameters.operationId;
        return request;
    }

    private createResponse(response: any, success: boolean, nonAnonymizableRegexesPaths: String[]): WidgetResponse {
        return this.responseFactory.createResponseFromRestApi(response, success, nonAnonymizableRegexesPaths)
    }

    private createSuccessResponse(response: any): WidgetResponse {
        return this.createResponse(response, true, this.nonAnonymizableRegexesPathsSuccess)
    }

    private createErrorResponse(errorResponse: any) {
        return throwError(
            this.createResponse(errorResponse, false, this.nonAnonymizableRegexesPathsError)
        )
    }

}