import { Component } from '@angular/core';
import { Platform } from '@ionic/angular';
import { AgentifaiPluginControllerService } from 'src/app/services/agentifai/agentifai-plugin-controller.service';
import { Api } from 'src/app/services/api/api.service';
import { StorageService } from 'src/app/services/storage/storage.service';
import { AssistantUtilsService } from 'src/app/services/assistant-utils/assistant-utils.service';
import { InternalUtils } from 'src/app/internal/internal-utils';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {
  constructor(
    platform: Platform,
    private api: Api,
    private agentifaiController: AgentifaiPluginControllerService,
    private storage: StorageService,
    private assistantUtils: AssistantUtilsService
  ) {
    platform.ready().then(() => {
      this.initializeAssistant()
      this.setConfigurations();
    });
  }

  private setConfigurations() {
    let headers = this.api.getHeadersAlice();
    let headersKeysMapping = this.api.HEADERS_KEYS_MAPPING_ALICE;
    let endpoints = InternalUtils.getEndpointsWhitelist();
    var endpointUrl = InternalUtils.endpoints[0].baseUrl;

    this.storage.getbaseUrl().then(result => {
      if (result != null) endpointUrl = result.baseUrl
      this.agentifaiController.setConfigurations({ url: endpointUrl, headers: headers, headersKeysMapping: headersKeysMapping, endpoints: endpoints });
    }, error => {
      this.agentifaiController.setConfigurations({ url: endpointUrl, headers: headers, headersKeysMapping: headersKeysMapping, endpoints: endpoints });
    });
  }

  private initializeAssistant() {
    this.assistantUtils.getConfig()
      .then( config => {
        this.agentifaiController.initializeWithConfig(config[0], config[1], config[2])
      })
  }

}
